.alert-body {
    margin: 20vh auto !important;
    padding: 5% 3% !important;
    max-height: 80vh;
    width: 50%;
    min-width: 300px;
    opacity: .9;
    z-index: 3000;
    font-size: calc(8px + .5vw);
}

#overlay {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: .92;
    z-index: 2000;
    text-align: center;
}

